<template>
  <v-container>
    <h1>Test Line Chart</h1>
      <GChart
        type="LineChart"
        :data="chartData"
        :options="chartOptions"
      />
      <h2>HandsomeTable chartData</h2>
      <hot-table :data="chartData" ref="chartData_ref" licenseKey="non-commercial-and-evaluation"></hot-table>
      <v-btn @click="update()">update</v-btn>
      <p></p>
      <p>chartData: {{chartData}}</p>
  </v-container>
</template>

<script>
import HotTable from 'vue-handsontable-official';

export default {
  components: {
    HotTable
  },
data() {
  return {
    chartData: [
      ['Year', 'Sales', 'Expenses'],
      ['2004', 1000, 400],
      ['2005', 1170, 460],
      ['2006', 660, 1120],
      ['2007', 1030, 540]
    ],
    chartOptions: {
      title: 'Company Performance',
      curveType: 'function',
      legend: { position: 'bottom' }
    }
  };
},
methods: {
    update: function() {
        this.chartData = this.$refs.chartData_ref.table.getData();
    }
}
};
</script>

