<template>
  <v-container>
    <h1>Test Radar_Chart</h1>
    <apexchart type=radar height=350 :options="chartOptions" :series="series"  v-model="data"/>
    <p></p>
    <h2>HandsomeTable chartData</h2>
    <hot-table :data="[chartOptions.labels,series[0].data]" ref="table_ref" licenseKey="non-commercial-and-evaluation"></hot-table>
    <v-btn @click="update()">update</v-btn>
    <p></p>
    <p>series.name: {{series[0].name}}</p>
    <p>series.data: {{series[0].data}}</p>
    <p>chartOptions.labels:{{chartOptions.labels}}</p>
    <p>table: {{table}}</p>
  </v-container>
</template>

<script>
import HotTable from 'vue-handsontable-official';

  export default {
    components: {
      HotTable
    },
    data: function () {
      return {
        chartOptions: {
          labels: ['January', 'February', 'March', 'April', 'May', 'June'],
          title: {
            text: 'Basic Radar Chart'
          }
        },
        series: [{
          name: 'Series 1',
          data: [80, 50, 30, 40, 100, 20],
        }],
        table: 0
      }
    },
methods: {
    update: function() {
        this.chartOptions.labels= this.$refs.table_ref.table.getDataAtRow(0);
        this.series[0].data= this.$refs.table_ref.table.getDataAtRow(1);
        this.table=this.$refs.table_ref.table.getData()
    }
},
  };
</script>

